.card {
  padding: 32px 16px 16px;
  width: $card-size;
  border: 1px solid $border-color;
  border-radius: 5px;
  background: $bg-color;
  align-items: center;

  &__picture {
    display: block;
    margin: 0 auto 40px;
    width: 160px;
    height: 134px;
  }

  &__title {
    color: $main-color;
    font-size: $mainFontSize;
    line-height: 18px;
    font-weight: 500;
    margin-bottom: 4px;
  }
  &:hover {
    color: $productCard-hover;
    transition-duration: 0.3s;
  }

  &__subtitle {
    color: $second-color;
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    margin-bottom: 16px;
  }

  &__reviews {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
  }

  &__reviews-rating {
    color: $main-color;
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
  }

  &__price-text {
    color: $second-color;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
  }

  &__price-number {
    color: $main-color;
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
  }

  &__button {
    display: flex;
    justify-content: center;
    box-sizing: border-box;
    width: 166px;
    height: 40px;
    cursor: pointer;
    border-radius: 5px;
    background: $accent-color;
    padding: 12px 70px;
    color: $bg-color;
    font-size: 14px;
    font-weight: 700;
    line-height: 16px;
    text-transform: uppercase;

    &:hover {
      box-sizing: border-box;
      border: 1px solid $accent-color;
      border-radius: 5px;
      color: $accent-color;
      background-color: $button-text-color-hover;
      transition-duration: 0.3s;
    }
  }
}
